import React, { useEffect, useState } from 'react'

import 'fullpage.js/dist/fullpage.min.css'
import fullpage from 'fullpage.js'
import Banner from 'components/Banner'
import { AppWrapper } from 'styled'
import FullpageOne from 'components/FullpageOne'
import FullpageTwo from 'components/FullpageTwo'
import FullpageThree from 'components/FullpageThree'
import FullpageFour from 'components/FullpageFour'
import FullpageFive from 'components/FullpageFive'
import Navigation from 'components/Navigation'

export function App () {
  const [activePage, setActive] = useState(0);
  useEffect(() => {
    new fullpage('#fullpage', {
      scrollHorizontally: true,
      normalScrollElements: '.scrollable',
      bigSectionsDestination: 'top',
      lazyLoading: true,
      afterLoad: (origin, destination) => {
        setActive(destination.index)
      }
    });
  }, [])

  const onClick = (index) => {
    if (!window.fullpage_api) { return }
    window.fullpage_api.moveTo(index)    
  }
  return (
    <AppWrapper>
      <div id="fullpage">
        <Banner></Banner>
        <FullpageOne></FullpageOne>
        <FullpageTwo></FullpageTwo>
        <FullpageThree></FullpageThree>
        <FullpageFour></FullpageFour>
        <FullpageFive></FullpageFive>
      </div>
      <Navigation num={6} active={activePage} onClick={onClick}></Navigation>
    </AppWrapper>
  )
}

export default App
