import React from 'react'
import { NavigationBox, NavigationItem } from './styled'

export default function Navigation({ num, active, onClick }) {

  const list = new Array(num).fill(false);
  const changeActive = (index) => {
    onClick(index)
  }
  return (
    <NavigationBox>
      {
        list.map((item, index) => {
          return (
            <NavigationItem
              onClick={() => changeActive(index + 1)}
              key={index} className={active === index ? 'active' : ''}>
              <div style={{ height: '100%'}}></div>
            </NavigationItem>
          )
        })
      }
    </NavigationBox>
  )
}
