import React from 'react'
import SectionBox from 'components/SectionBox'
import { WrapperOne, CenterOneTitle, CenterOneSub } from './styled'

export default function FullpageOne() {
  const SectionOne = (
    <WrapperOne>
      <CenterOneTitle>拒绝空谈，直击行业痛点！</CenterOneTitle>
      <CenterOneSub>作为独立第三方，汇聚智享懂你所忧，先你所想，圆你所愿！</CenterOneSub>
    </WrapperOne>
  )
  
  return (
    <SectionBox title={'为什么选择我们？'} center={ SectionOne } top="1rem"></SectionBox>
  )
}
