import styled from 'styled-components'
import back from 'assets/image/back.png'

export const SectionWrapper = styled.div`
  position: relative;
  height: 100vh;
  padding: .8rem 1.56rem;
  box-sizing: border-box;
`

export const SectionTitle = styled.div`
  font-size: .55rem;
  background-image: linear-gradient(to bottom, #fff, #9fa5c6);
  -webkit-background-clip: text;
  color: transparent;
`
export const CenterWrapper = styled.div`
  position: relative;
  text-align: center;
`

export const SectionCenter = styled.div`
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  margin: 0 auto;
  background: rgba(255, 255, 255, .1);
  text-align: left;
`
export const LeftTop = styled.div`
position: absolute;
top: 0;
left: 0;
width: .35rem;
height: .35rem;
border-width: .07rem;
border-style: solid;
border-color: #00b8ff transparent transparent #00b8ff;
`
export const LeftBottom = styled.div`
position: absolute;
bottom: 0;
left: 0;
width: .35rem;
height: .35rem;
border-width: .07rem;
border-style: solid;
border-color: transparent transparent #00b8ff #00b8ff;
`
export const RightTop = styled.div`
position: absolute;
top: 0;
right: 0;
width: .35rem;
height: .35rem;
border-width: .07rem;
border-style: solid;
border-color: #00b8ff #00b8ff transparent transparent;
`
export const RightBottom = styled.div`
position: absolute;
bottom: 0;
right: 0;
width: .35rem;
height: .35rem;
border-width: .07rem;
border-style: solid;
border-color: transparent #00b8ff #00b8ff transparent;
`