import React from 'react'
import Navbar from 'components/Navbar'
import { BannerBox, BannerInner,BannerSub,BannerTitle, BannerTitleBox, BannerBack, BannerCenter, CenterLeft, CenterRight, ImgWrap, CenterTitle, CenterSub, BannerBtn, ButtonIcon } from './styled'
import background from 'assets/image/background.png'
import icon1 from 'assets/image/icon1.png'
import icon2 from 'assets/image/icon2.png'
import icon3 from 'assets/image/icon3.png'

export default function Banner() {
  const getMore = () => {
    if (!window.fullpage_api) { return }
    window.fullpage_api.moveTo(2)
  }
  return (
    <div className="section fp-auto-height">
      <div className="scrollable">
        <Navbar></Navbar>
        <BannerBox>
          <BannerInner>
            <BannerBack>
              <img src={background}></img>
            </BannerBack>
            <BannerTitleBox>
              <BannerTitle>
                我们能为你做什么？
              </BannerTitle>
              <BannerSub>专注于为用工企业、灵活用工供应商（委托代征主体）提供综合科技服务</BannerSub>
            </BannerTitleBox>
            <BannerCenter>
              <CenterLeft>
                <ImgWrap>
                  <img src={icon1} alt="icon1"></img>
                </ImgWrap>
                <CenterTitle>智享汇—赋能用工企业</CenterTitle>
                <CenterSub>为您匹配合规诚信的灵活用工供应商和自由职业者实现资金安全、极低成本、稳定合规的人才共享</CenterSub>
              </CenterLeft>
              <CenterRight>
                <ImgWrap>
                  <img src={icon2} alt="icon2"></img>
                </ImgWrap>
                <CenterTitle>智赋能—赋能灵活用工供应商</CenterTitle>
                <CenterSub>解决委托代征主体在资金安全、税务、反洗钱风控及技术对接等专业问题，定期提供交易对手风险评估报告，以科技彰显灵活用工领域的竞争力</CenterSub>
              </CenterRight>
            </BannerCenter>
          </BannerInner>
          <BannerBtn onClick={getMore}>
            了解更多
            <ButtonIcon>
              <img src={icon3}></img>
            </ButtonIcon>
          </BannerBtn>
        </BannerBox>
      </div>
    </div>
  )
}
