import styled from 'styled-components'

export const NavigationBox = styled.div`
  position: fixed;
  right: 17px;
  position: fixed;
  z-index: 100;
  top: 50%;
  opacity: 1;
  transform: translateY(-50%);
`
export const NavigationItem = styled.div`
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  margin: 12px;
  padding: 2px;
  border: 2px solid #fff;
  border-radius: 7px;

  &.active {
    background: #fff;
  }
`
