import React from 'react'
import { CenterWrapper, LeftBottom, LeftTop, RightBottom, RightTop, SectionCenter, SectionTitle, SectionWrapper } from './styled'

export default function SectionBox({ title, center, left, right, top = 0 }) {
  return (
    <div className="section">
      <SectionWrapper>
        <SectionTitle>{ title }</SectionTitle>
        <CenterWrapper style={{marginTop: top}}>
          <SectionCenter>
            { center }
            <LeftTop></LeftTop>
            <LeftBottom></LeftBottom>
            <RightTop></RightTop>
            <RightBottom></RightBottom>
          </SectionCenter>
          {
            left ? left: null
          }
          {
            right ? right: null
          }
        </CenterWrapper>
      </SectionWrapper>
    </div>
  )
}
