import SectionBox from 'components/SectionBox'
import React from 'react'
import { Left, Right, Title } from '../FullpageTwo/styled'
import { Content, Wrapper, SubTitle, SubWrapper } from './styled'

export default function FullpageThree() {
  const Center = (
    <Wrapper>
      <Title>汇聚智享-更合规</Title>
      <Content>金融级别反洗钱风控系统，拒绝人为干扰！</Content>
      <SubWrapper>
        <Content>
          <SubTitle>事前准入</SubTitle>交易前遵循金融机构KYC (Know Your Clients) 制度，严格甄别客户业务场景真实性。
        </Content>
      </SubWrapper>
      <SubWrapper>
        <Content>
          <SubTitle>事中控制</SubTitle>交易中对接反洗钱系统对交易持续实施监控预警、挂单和拒绝发放等风控措施。
        </Content>
      </SubWrapper>
      <SubWrapper>
        <Content>
          <SubTitle>事后分析</SubTitle>交易后对存量数据以反洗钱业务逻辑进行精准分析。
        </Content>
      </SubWrapper>
      <SubWrapper>
        <Content>
          <SubTitle>全程监控</SubTitle>全程对接舆情监控系统，监管动态和重大舆情预警快人一步。
        </Content>
      </SubWrapper>
    </Wrapper>
  )

  const LeftItem = (
    <Left>用工企业<br/>支付账户是否存在资金安全隐患，供应商会不会卷走项目资金?</Left>
  )
  const RightItem = (
    <Right>灵活用工供应商<br/>客户担心项目资金被挪用？业务无法扩大？</Right>
  )
  
  return (
    <SectionBox 
      title={'交易“智”风控——注入金融机构基因，业务智享无忧！'} 
      center={ Center }
      left={ LeftItem }
      right={ RightItem }
      top=".6rem"
    ></SectionBox>
  )
}
