import styled from 'styled-components'

export const BannerBox = styled.div`
  position: relative;
  padding: .77rem 0;
  background: #fff;
`
export const BannerInner = styled.div`
  position: relative;
  height: 7.25rem;
  &::before, &::after {
    content: '';
    display: block;
    clear: both;
  }
`
export const BannerTitleBox = styled.div`
  position: absolute;
  top: .56rem;
  left: 50%;
  padding-left: .49rem;
`
export const BannerTitle = styled.div`
  font-size: .4rem;
`
export const BannerSub = styled.div`
  margin-top: .12rem;
  font-size: .17rem;
`
export const BannerBack = styled.div`
  float: left;
  width: 50%;
`
export const BannerCenter = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 13.5rem;
  box-shadow: 0 0 21px 2px #ddd;
`
export const CenterLeft = styled.div`
  display: inline-block;
  width: 50%;
  padding: .66rem 0 .93rem;
  text-align: center;
  background: linear-gradient(140deg, rgb(45,115,238) 0%, rgb(84,184,252) 100%);
  font-size: 0;
  color: #fff;
`
export const CenterRight = styled.div`
  display: inline-block;
  width: 50%;
  padding: .66rem 0 .93rem;
  text-align: center;
  font-size: 0;
  color: #000;
`
export const ImgWrap = styled.div`
  display: inline-block;
  width: .76rem;
`
export const CenterTitle = styled.div`
  margin-top: .39rem;
  font-size: .27rem;
`
export const CenterSub = styled.div`
  margin-top: .39rem;
  padding: 0 .69rem;
  font-size: .15rem;
`
export const BannerBtn = styled.div`
  position: absolute;
  bottom: 1.21rem;
  right: 2.64rem;
  height: .63rem;
  line-height: .63rem;
  padding-left: .82rem;
  padding-right: 1.17rem;
  border-radius: 5px;
  background: #fe9f3a;
  font-size: .2rem;
  color: #fff;
`
export const ButtonIcon = styled.div`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: .82rem;
  width: .2rem;
  line-height: 0;
`